import common from '../../common/common.js'
import util from '../../common/util.js'

export default {
	data() {
		return {
			baseUrl:common.baseUrl,
			id:'',
			shopid:0,
			shop:[],
			userInfo:[],
			goods:[],		//商品信息
			indicatorDots: true,
			indicatorColor: 'rgba(0, 0, 0, .3)',
			indicatorActiveColor:'#ffffff',
			autoplay: true,
			interval: 4000,
			duration: 500,
			picList:[],
			comment_list:[],    //评论列表
			cartnums:0,         //购物车中的商品数量
			stocks:0,           //库存
			hideitem:1,
			guige:[],
			guige_name:'',       //选择的规格名称
			guige_key: -1,     //选中的规格键值
			guige_price:0,         //选中规格的价格
			limitnums:0,             //商品限购，大于0表示限购
			nums:1            ,//购买数量
			pophide: 'hide',
			popshare: 'hide',
			popsharehb: 'hide',
			shareImg:'',
			canbuy:1,		//能否下单
			
			info:{
				bg_color:'0,170,250',
				bg_alpha:1,
				delta:2,
				icon_color:'white',
				shpid:0,
			},
			
			mycartList:[],			//购物车
			totalnums:0,			//总数量
			totalamount:0.00,		//总金额
			
			showForm:false
			
		}
	},
	onLoad(option) {
		var that = this
		var id = option.id
		// id = 536;
		that.id = id;
		var share = option.share
		if(share == 1){
			//分享
			that.info.delta = 1
		}
		that.userInfo = uni.getStorageSync('userInfo');
		
		that.getGoodsInfo();
		that.getCartNums();
		
		var mycartList = uni.getStorageSync('mycartList');
		var timestamp = Date.parse(new Date());
		var mycartList_expire = uni.getStorageSync('mycartList_expire');
		if( timestamp > mycartList_expire && mycartList){
			//过期
			uni.setStorageSync('mycartList', '');
			uni.setStorageSync('mycartList_expire', '');
		}
		that.mycartList = mycartList != '' ? mycartList : []
		
		var pages = getCurrentPages();
		that.info.delta = pages.length
		
		setTimeout(function(){
			that.updateCartInfo();
		},1000)
	},
	onPullDownRefresh() {
		var that = this
		uni.showNavigationBarLoading();
		
		that.getGoodsInfo();
		
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},
	onShareAppMessage(res) {
		var that = this
		if (res.from === 'button') {// 来自页面内转发按钮
		  console.log(res.target)
		}
		var userInfo = uni.getStorageSync('userInfo');
		var goods = that.goods
		if(userInfo.users_id > 0){
			var shareUrl = '/pages/merchant/goods?id='+that.id+'&tjuid='+userInfo.users_id
		}else{
			var shareUrl = '/pages/merchant/goods?id='+that.id
		}
		var share_price = that.goods.goods_price > 0 ? '¥'+ that.goods.goods_price : ''
		return {
		  title: that.goods.goods_title + share_price +' - '+goods.shop_title,
		  path: shareUrl,
		}
	},
	methods: {
		
		//申请查看
		shenqingView(){
			var that = this
			var userInfo = uni.getStorageSync('userInfo');
			console.log("userInfo",userInfo.users_id)
			if(userInfo.users_id  > 0 ){
				that.toggleForm();
				
			}else{
				uni.showModal({
					title: '提示',
					showCancel: false,
					content: '亲，您还没有登录，现在去登录',
					success: function (res) { 
					  uni.navigateTo({
						url: '/pages/userlogin/userlogin?tfrom=products&id='+that.shopid,
						success:function(res){
							console.log(res);
						}
					  })
					}
				})
			}
		},
		
		//提交申请查看表单
		formSubmit(e){
			var that = this
			var data = e.detail.value;
			var title = data.title
			var addr = data.addr
			var contacter = data.contacter
			var mobile = data.mobile
			//检测是否有推荐人
			var userInfo = uni.getStorageSync('userInfo');
			console.log('aaa');
			
			if(title == ''){					
				uni.showToast({
					title: '请输入店铺名称',duration: 1500,icon :'none'
				});
				return false
			}
			
			if(addr == ''){
				uni.showToast({
					title: '请输入店铺地址',duration: 1500,icon :'none'
				});
				return false
			}
			
			if(contacter == ''){
				uni.showToast({
					title: '请输入联系人',duration: 1500,icon :'none'
				});
				return false
			}
			
			if(mobile.length != 11){
				uni.showToast({
					title: '请输入联系人手机',duration: 1500,icon :'none'
				});
				return false
			}
			
			uni.showLoading({
				title: '提交中'
			});
			
			uni.request({
				url: common.apiUrl + 'shop/shenqingViewShopPrice',
				method: 'GET',
				data: {
					shopid:that.shopid,
					userid:userInfo.users_id,
					title:title,
					addr:addr,
					contacter:contacter,
					mobile:mobile,
				},
				success: res => {
					uni.hideLoading();
					var data = res.data
					console.log(data);
					if(data.code == 0){						
						uni.showToast({
							title: '提交成功，等待审核',
							duration: 1500,
							icon:'success'
						});
						setTimeout(function(){
							that.toggleForm();							
						},1500)
					}else{
						uni.showToast({
							title: data.msg,
							duration: 1500,
							icon:'none'
						});
						return false
					}
				},
				fail: () => {},
				complete: () => {}
			});
			
			
			
		},
			
			
		
		
		toggleForm(){
			var that = this
			that.showForm = !that.showForm;
		},
		
		
		goodsoperate(e){
			var that = this
			console.log(e);
			var guige = that.guige
			
			if(guige == null || guige == ''){
				that.operate(e);
				
			}else{					
				console.log("guige",guige);
				that.popCart();
			}
			
		},
		//加入购物车
		operate(e){
			//wx.setStorageSync('mycartList', '');
			var that = this
			var target = e.currentTarget.dataset
			var id = target.id;
			var sindex = target.sindex;
			var index = target.index;
			var op = target.op;
			var price = target.price;
			var title = target.title;
			var stocks = target.stocks;
			var mycartList = that.mycartList;
			var userInfo = uni.getStorageSync('userInfo');
			
			
			if( stocks <= 0){
				uni.showToast({
					title:'库存不足',
					duration:1500,
					icon:'none'
				})
				return false
			}
			
			var ishas = 0;
			
			if (op == 'addOne'){//添加
					console.log("index",index);				
					
					var t_temai = 0;
					for(var i=0;i<mycartList.length;i++){
						if(mycartList[i][1] == id){
							if( stocks < mycartList[i][2]){
								uni.showToast({
									title:'库存不足',
									duration:1500,
									icon:'none'
								})
								return false
							}
							mycartList[i][2]++;							
							ishas = 1;
						}
					}
						
						console.log("mycartList",mycartList)				
					if(ishas == 0){
						var temparr = [];
						temparr.push(that.goods.goods_shopid);		//店铺ID
						temparr.push(id);						//商品ID
						temparr.push(1);						//数量
						temparr.push(price);					//价格
						temparr.push(title);					//标题
						temparr.push('');					//规格Key
						temparr.push('');					//规格名称
						console.log(temparr)
						mycartList.push(temparr);
					}
					
					
			}
			if(op == 'subOne'){
				for(var i=0;i<mycartList.length;i++){
					if(mycartList[i][1] == id){
						mycartList[i][2]--;
						ishas = 1;
						if(mycartList[i][2] == 0){
							mycartList.splice(i,1);
						}
					}
				}
			}
			console.log(mycartList);
			
			that.updateCartInfo();
			
			//wx.setStorageSync('mycartList', mycartList);
			var timestamp = Date.parse(new Date());
			var expire = timestamp + 3600000
			uni.setStorageSync('mycartList', mycartList);
			uni.setStorageSync('mycartList_expire', expire);
			
		},
		
		//更新购物车信息
		updateCartInfo(){
			var that = this
			var totalamount = 0;
			var totalnums = 0;
			var mycartList = that.mycartList;
			//var activity = that.activity
						
			var all_dis = 0;
			for (let j = 0; j < mycartList.length; j++) {
			  
			  totalnums += mycartList[j][2];
			  //var tt = parseFloat(nums_arr[j]) * parseFloat(price_arr[j]);				 
			  totalamount += mycartList[j][2]*mycartList[j][3];
			  var t_amount = mycartList[j][2]*mycartList[j][3];
			  t_amount = t_amount.toFixed(2)
			}		
			totalamount = Math.round(totalamount * 100) / 100;		
		
			that.totalnums = totalnums;
			that.totalamount = totalamount;
			
		},
		popShare: function () {
			var that = this
			that.popshare= '';
		},

		closeShare: function () {
			var that = this
			that.popshare= 'hide';
		},

		popShareHB: function () {
			var that = this
			that.popshare= '';
			that.popsharehb= '';
			that.getShareHB();
		},

		closeSharehb: function () {
			var that = this
			that.popshare= 'hide';
			that.popsharehb= 'hide';
		},

		getShareHB: function () {
			var that = this
			that.getGoodsQR();
		},

		getGoodsQR: function () {
			var that = this
			var userInfo = uni.getStorageSync("userInfo");
			var url = common.baseUrl + 'sharepic/getGoodsShareImg';
			uni.showToast({
				title: '加载数据中',
				icon: 'loading',
				duration: 1000
			})
			uni.request({
				url: url,
				data: {
					goodsid: that.goods.goods_id,
					uid: userInfo.users_id,
				},
				method: 'GET',
				success: function (res) {
					console.log(res);
					that.shareImg = res.data.result
					that.popsharehb = '';
				}

			});
		},

		save: function () {
			var that = this
			uni.showToast({
				title: '处理中',
				icon: 'loading',
				duration: 1000
			})
			uni.downloadFile({
				url: that.baseUrl + that.shareImg,
				success: function (res) {
					var tempFilePath = res.tempFilePath
					uni.getSetting({
						success(res) {
							if (!res.authSetting['scope.writePhotosAlbum']) {
								uni.authorize({
									scope: 'scope.writePhotosAlbum',
									success() {
										console.log('授权成功');
										uni.saveImageToPhotosAlbum({
											filePath: tempFilePath,
											success(res) {
												uni.showModal({
													title: '保存成功',
													content: '您可以用相册中的图片分享到朋友圈啦',
													showCancel: false,
													success: function (res) {
														that.closeShare();
														that.closeSharehb();
													}
												})
											},
										})
									},
									fail() {
										console.log('授权失败');
									}
								})
							} else {
								//console.log('保存图片')
								//console.log(tempFilePath);
								uni.saveImageToPhotosAlbum({
									filePath: tempFilePath,
									success(res) {
										uni.showModal({
											title: '保存成功',
											content: '您可以用相册中的图片分享到朋友圈啦',
											showCancel: false,
											success: function (res) {
												that.closeShare();
												that.closeSharehb();
											}
										})
									},
									fail(res) {
										console.log(res)
									},
									complete(res) {
										console.log(res)
									}
								})
							}

						}
					})
				}
			})

		},
		//关闭弹出层
		  closePopCart:function(){
			var that = this
			that.hideitem = 1;
		  },

		  //弹出
		  popCart:function(){
			var that = this
			that.hideitem = 0;
		  },
		  
		  //加入购物车
		  addToCart:function(){
			var that = this
			that.checkAttr();
			if (that.guige_key == -1) {
			  wx.showToast({
				title: '请选择规格',
				icon: 'none',
				duration: 1000
			  })
			  return false
			}

		   

			var userInfo = uni.getStorageSync("userInfo");
			var id = that.goods.goods_id;
			var title = that.goods.goods_title;
			var nums = that.nums;
			var guige_name = that.guige_name;
			var price = that.guige_price;

			
			if (userInfo.users_id > 0) {
			  
			  uni.showToast({
				title: '加载中',
				icon: 'loading',
				duration: 1000
			  })
			  var guige = that.guige
			  var kindex = that.guige_key;
			  var stocks = guige[kindex][3];
			  var mycartList = that.mycartList;
			  var userInfo = uni.getStorageSync('userInfo');
			  
			  
			  if( stocks <= 0 || stocks < nums){
			  	uni.showToast({
			  		title:'库存不足',
			  		duration:1500,
			  		icon:'none'
			  	})
			  	return false
			  }
			  
			  var ishas = 0;
			  
			  for(var i=0;i<mycartList.length;i++){
			  	if(mycartList[i][1] == id && mycartList[i][5] == kindex){
			  		if( stocks < mycartList[i][2]+nums){
			  			uni.showToast({
			  				title:'库存不足',
			  				duration:1500,
			  				icon:'none'
			  			})
			  			return false
			  		}
			  		mycartList[i][2] = mycartList[i][2] + nums;							
			  		ishas = 1;
			  	}
			  }
			  	
			  	console.log("mycartList",mycartList)				
			  if(ishas == 0){
			  	var temparr = [];
			  	temparr.push(that.goods.goods_shopid);		//店铺ID
			  	temparr.push(id);						//商品ID
			  	temparr.push(1);						//数量
			  	temparr.push(price);					//价格
			  	temparr.push(title);					//标题
			  	temparr.push(kindex);					//规格KEY
			  	temparr.push(guige_name);				//规格名称
			  	console.log(temparr)
			  	mycartList.push(temparr);
			  }
			  console.log(mycartList);
			  
			  that.updateCartInfo();
			  
			  //wx.setStorageSync('mycartList', mycartList);
			  var timestamp = Date.parse(new Date());
			  var expire = timestamp + 3600000
			  uni.setStorageSync('mycartList', mycartList);
			  uni.setStorageSync('mycartList_expire', expire);
			  that.closePopCart();

			} else {
			  //app.checkUserLogin();
			  uni.showModal({
				title: '提示',
				showCancel: false,
				content: '亲，您还没有登录，现在去登录',
				success: function (res) { 
				  uni.navigateTo({
					url: '/pages/userlogin/userlogin?tfrom=goods&id='+that.id,
					success:function(res){
						console.log(res);
					},
					fail(res) {
						console.log(res)
					},
					complete(res) {
						console.log(res)
					}
				  })
				}
			  })

			}
		  },

		  //参数验证
		  checkAttr:function(){
			var that = this

			if(that.guige_name == ''){
			  uni.showToast({
				title: '请选择规格',
				icon: 'loading',
				duration: 1000
			  })
			  return false
			}else{
			  return true
			}
			
		  
		  },
		  
		  //获取购物车中商品数量
		  getCartNums:function(){
			var that = this
			var userInfo = wx.getStorageSync("userInfo");
			var uid = userInfo.mem_id
			if(uid > 0){
				uni.request({
					url: common.apiUrl + 'getCartNums',
					header: {
					  "Content-Type": "application/x-www-form-urlencoded"
					},
					method: 'POST',
					data: {
						uid:uid
					},
					success: res => {
						that.cartnums = res.data.cartnums
					},
					fail: () => {},
					complete: () => {}
				});				  
			}else{
			  that.cartnums = 0;
			}
		  },
		  
		  //购物车数量加
		  cartNumsADD:function(){
			var that = this
			var nums = that.nums
			var limitnums = that.limitnums
			var stocks = that.stocks
			
			if(limitnums > 0){
			  //有限购
			  if(nums < limitnums){
				if(nums < stocks){
				  //有库存
				  nums++
				  that.nums = nums
				}else{
				  //库存存不足
				  wx.showToast({ title: '库存不足',icon: 'loading',duration: 1000 })
				  return false
				}        
			  }else{
				//超过限购数量
				wx.showToast({ title: '超过限购数量', icon: 'loading', duration: 1000 })
				return false
			  }
			}else{
			  if (nums < stocks) {
				//有库存
				nums++
				that.nums = nums
			  } else {
				//库存存不足
				wx.showToast({ title: '库存不足', icon: 'loading', duration: 1000 })
				return false
			  }
			  
			}
			//console.log(that.data.nums)
		  },

		  //购物车数量减
		  cartNumsJIAN: function () {
			var that = this
			var nums = that.nums
			var limitnums = that.limitnums
			var stocks = that.stocks

			if(nums > 1){
			  nums--
			  that.nums =  nums
			}else{
			  return false;
			}
		  },

		  //手动更新数量
		  updateCartNums:function(e){
			var that = this
			var nums = parseInt(e.detail.value);
			var limitnums = that.limitnums
			var stocks = that.stocks
			
			if (nums > 0){
			  if (limitnums > 0) {
				//有限购
				if (nums <= limitnums) {
				  if (nums <= stocks) {
					//有库存          
					that.nums =  nums
				  } else {
					//库存存不足
					wx.showToast({ title: '库存不足', icon: 'loading', duration: 1000 })
					that.nums =  stocks
					return false
				  }
				} else {
				  //超过限购数量
				  wx.showToast({ title: '超过限购数量', icon: 'loading', duration: 1000 })
				  that.nums =  limitnums
				  return false
				}
			  } else {
				if (nums <= stocks) {
				  //有库存        
				  that.nums =  nums
				} else {
				  //库存存不足
				  wx.showToast({ title: '库存不足', icon: 'loading', duration: 1000 })
				  return false
				}

			  }
			}else{
			  that.nums =  1
			}
		  },
		  
		//选择规格
		  chooseGuige:function(e){
			var that = this
			var key = e.currentTarget.dataset.key
			var guige = e.currentTarget.dataset.guige
			var price = e.currentTarget.dataset.price			
			
			if (key == that.guige_key){
				that.guige_name = ''
				that.guige_key = -1
				that.guige_price = 0
				that.stocks = 0
			}else{
				that.guige_name = guige
				that.guige_key = key
				that.guige_price = price
				that.stocks = that.guige[key][3]
			}
			
		  },
		//返回首页
		  gohome:function(){
		    var that = this
			uni.navigateTo({
			  url: '/pages/merchant/shop?id='+that.info.shopid
			})
		  },
		  
		  calling: function (e) {
			var phone = e.currentTarget.dataset.phone
			uni.makePhoneCall({
			  phoneNumber: phone //
			})
		  },

		  //跳转购物车
		  gotocart:function(){
			  var that = this
			  var totalnums = that.totalnums
			  if(totalnums == 0){
				  return false
			  }
			uni.navigateTo({
			  url: '/pages/cart/cart'
			})
		  },
		
		callphone: function (e) {
			var phone = e.currentTarget.dataset.phone
			uni.makePhoneCall({
			  phoneNumber: phone //
			})
		},
		//获取商品信息
		getGoodsInfo(){
			var that = this
			uni.request({
				url: common.apiUrl + 'shop/getGoodsInfo',
				method: 'GET',
				data: {
					id:that.id
				},
				success: res => {
					var data = res.data
					that.goods = data.goods
					that.guige = data.guige
					console.log("guige",data.guige)
					that.stocks = res.data.goods.goods_stocks
					that.picList = res.data.pics_list
					that.limitnums = res.data.goods.goods_maxnums
					that.canbuy = res.data.canbuy
					uni.setNavigationBarTitle({
					    title: data.goods.goods_title
					});
					that.info.title = data.goods.goods_title
					that.info.shopid = data.goods.goods_shopid
					that.shopid = data.goods.goods_shopid
					if(that.shopid > 0){
						//获取店铺信息
						that.getShopInfo();
					}











				},
				fail: () => {},
				complete: () => {}
			});
		},
		//获取店铺信息
		getShopInfo(){
			var that = this
			var shopid = that.shopid
			var userInfo = uni.getStorageSync('userInfo');
			if(shopid == '' || shopid == 0){
				return false
			}
			uni.request({
				url: common.apiUrl + 'shop/getShopInfo',
				method: 'GET',
				data: {
					shopid:shopid,
					userid:userInfo.users_id
				},
				success: res => {
					that.shop = res.data.shop
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		//获取商品评论
		getGoodsCommentList(){
			var that = this
			uni.request({
				url: common.apiUrl + 'getGoodsCommentList',
				method: 'GET',
				data: {
					goodsid:that.id
				},
				success: res => {
					var data = res.data
					that.comment_list = data.comment_list
				},
				fail: () => {},
				complete: () => {}
			});
		},
	}
}
